.GithubUsernameForm {
    width: 400px;
    display: flex;
    align-items: center;
    height: 32px;
    margin: 0 auto;
    margin-top: 16px;
    margin-bottom: 48px;
}

.GithubUsernameForm input {
    flex: 1;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
    font-size: 12px;
    height: 100%;
    margin-right: 16px;
}

.GithubUsernameForm button {
    background: black;
    color: white;
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 4px;
    font-size: 16px;
    padding-left: 16px;
    padding-right: 16px;
    height: 100%;
    font-weight: bold;
}

.GithubUsernameForm button:hover {
    background: #495057;
}